html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
}

h2 {
  font-size: 2rem;
}
@media (min-width: 320px) {
  h2 {
    font-size: 2.25rem;
  }
}
@media (min-width: 481px) {
  h2 {
    font-size: 2.5rem;
  }
}
@media (min-width: 768px) {
  h2 {
    font-size: 2.75rem;
  }
}
@media (min-width: 1025px) {
  h2 {
    font-size: 3rem;
  }
}
@media (min-width: 1201px) {
  h2 {
    font-size: 3.5rem;
  }
}

h3 {
  font-size: 1.5rem;
}
@media (min-width: 320px) {
  h3 {
    font-size: 1.75rem;
  }
}
@media (min-width: 481px) {
  h3 {
    font-size: 1.9rem;
  }
}
@media (min-width: 1025px) {
  h3 {
    font-size: 2.2rem;
  }
}
@media (min-width: 1201px) {
  h3 {
    font-size: 2.5rem;
  }
}

p {
  font-size: 1.2rem;
}
@media (min-width: 320px) {
  p {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  p {
    font-size: 1.5rem;
  }
}
@media (min-width: 1025px) {
  p {
    font-size: 2rem;
  }
}
@media (min-width: 1201px) {
  p {
    font-size: 2.25rem;
  }
}

ul {
  padding-left: 0;
}
ul p {
  padding-left: 2rem;
  padding-right: 2rem;
}
@media (min-width: 768px) {
  ul {
    font-size: 2rem;
  }
}

li {
  list-style: none;
  font-size: 1.2rem;
}
@media (min-width: 320px) {
  li {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  li {
    font-size: 1.5rem;
  }
}
@media (min-width: 1025px) {
  li {
    font-size: 1.7rem;
  }
}
@media (min-width: 1201px) {
  li {
    font-size: 1.9rem;
  }
}

form p {
  font-size: 1.1rem;
}
@media (min-width: 320px) {
  form p {
    font-size: 1.35rem;
  }
}
@media (min-width: 481px) {
  form p {
    font-size: 1.45rem;
  }
}
@media (min-width: 1025px) {
  form p {
    font-size: 1.65rem;
  }
}
@media (min-width: 1201px) {
  form p {
    font-size: 1.85rem;
  }
}

a {
  font-size: 1.25srem;
  text-decoration: none;
  color: unset;
}
@media (min-width: 1201px) {
  a {
    font-size: 1.5rem;
  }
}

.table {
  width: 90%;
  margin: auto;
}
@media (min-width: 481px) {
  .table {
    width: 70%;
  }
}
@media (min-width: 1025px) {
  .table {
    width: 60%;
  }
}
@media (min-width: 1201px) {
  .table {
    width: 50%;
  }
}

td {
  font-size: 1.1rem;
}
@media (min-width: 481px) {
  td {
    font-size: 1.25rem;
  }
}
@media (min-width: 1025px) {
  td {
    font-size: 1.5rem;
  }
}

tr {
  vertical-align: middle;
}

button,
.btn {
  font-size: 1.25rem;
}
@media (min-width: 320px) {
  button,
.btn {
    font-size: 1.5rem;
  }
}
.directory-btn {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
@media (min-width: 1201px) {
  .directory-btn {
    align-items: flex-start;
  }
}

.form-select,
.form-control,
.invalid-feedback {
  font-size: 1rem;
}
@media (min-width: 320px) {
  .form-select,
.form-control,
.invalid-feedback {
    font-size: 1.25rem;
  }
}
@media (min-width: 768px) {
  .form-select,
.form-control,
.invalid-feedback {
    font-size: 1.75rem;
  }
}
label {
  font-size: 1.5rem;
}
@media (min-width: 320px) {
  label {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) {
  label {
    font-size: 2rem;
  }
}
.header-top {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 5rem;
  background-color: #efc3d0;
}
@media (min-width: 320px) {
  .header-top {
    justify-content: space-between;
  }
}
.header-phone {
  display: none;
  align-items: center;
}
@media (min-width: 320px) {
  .header-phone {
    display: flex;
  }
}
.header-phone > * {
  margin: 1rem;
}
.header-phone .img-container {
  height: 2rem;
  padding-left: 1rem;
}
@media (min-width: 768px) {
  .header-phone .img-container {
    padding-left: 3rem;
  }
}
.header-phone .img-container img {
  height: 100%;
}

.header-social-media {
  display: flex;
  align-items: center;
}
.header-social-media .img-container {
  height: 2rem;
  margin: 1.5rem;
}
@media (min-width: 768px) {
  .header-social-media .img-container {
    margin: 3rem;
  }
}
.header-social-media .img-container:last-child {
  padding-right: 1rem;
}
@media (min-width: 768px) {
  .header-social-media .img-container:last-child {
    padding-right: 2rem;
  }
}
.header-social-media .img-container img {
  height: 100%;
}
.header-middle {
  height: 15rem;
  background-color: #e76d98;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header-middle {
    height: 20rem;
  }
}
.header-middle .img-container {
  height: 7.5rem;
}
@media (min-width: 768px) {
  .header-middle .img-container {
    height: 12.5rem;
  }
}
.header-middle .img-container img {
  height: 100%;
}

.navbar {
  color: #e76d98;
  font-family: "RomanSerif";
  padding-bottom: 0;
  padding-top: 0;
}
.navbar .container-fluid {
  padding: 0;
}
.navbar .navbar-toggler {
  margin: 1rem auto;
  padding: 0.75rem;
}

@media (min-width: 768px) {
  .navbar-expand-md .navbar-collapse {
    display: block !important;
  }
}
.navbar-toggler:focus {
  box-shadow: none;
}

.header-bottom {
  height: auto;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .header-bottom {
    height: 7rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom {
    height: 10rem;
  }
}
.header-bottom .navbar {
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
@media (min-width: 768px) {
  .header-bottom .navbar {
    flex-direction: row;
  }
}
.header-bottom .navbar .navbar-element {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
@media (min-width: 768px) {
  .header-bottom .navbar .navbar-element {
    width: 20%;
    height: 7rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .navbar-element {
    height: 10rem;
  }
}
.header-bottom .navbar .navbar-element .img-container {
  height: 2rem;
}
@media (min-width: 768px) {
  .header-bottom .navbar .navbar-element .img-container {
    height: 2rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .navbar-element .img-container {
    height: 3rem;
  }
}
.header-bottom .navbar .navbar-element .img-container img {
  height: 100%;
}
.header-bottom .navbar .navbar-element p {
  font-size: 1.5rem;
  margin: 1.35rem;
}
@media (min-width: 768px) {
  .header-bottom .navbar .navbar-element p {
    font-size: 1.8rem;
    margin-right: 0;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .navbar-element p {
    font-size: 2.25rem;
  }
}
@media (min-width: 1201px) {
  .header-bottom .navbar .navbar-element p {
    font-size: 2.5rem;
    margin: 2rem;
  }
}
.header-bottom .navbar a {
  text-decoration: none;
}
.header-bottom .navbar a:hover {
  color: #f2afc6;
}

footer {
  background-color: #e76d98;
}
footer .footer-grid {
  display: grid;
  grid-template-columns: 1fr;
  padding-top: 2rem;
}
@media (min-width: 481px) {
  footer .footer-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) {
  footer .footer-grid {
    padding: 0;
    margin: 5rem 4rem 0 4rem;
  }
}
@media (min-width: 1025px) {
  footer .footer-grid {
    margin: 5rem 5rem 5rem 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1201px) {
  footer .footer-grid {
    margin: 2rem 7.5rem;
  }
}
footer .footer-grid .form-info {
  display: flex;
  align-items: center;
  margin: 2rem;
}
footer .footer-grid .form-info .img-container {
  display: flex;
  justify-content: center;
  width: 4rem;
  padding: 0.5rem;
}
@media (min-width: 768px) {
  footer .footer-grid .form-info .img-container {
    width: 4.5rem;
  }
}
@media (min-width: 1025px) {
  footer .footer-grid .form-info .img-container {
    width: 4rem;
  }
}
footer .footer-grid .form-info .img-container img {
  width: 100%;
}
footer .footer-grid .form-info .info-text {
  color: #ffffff;
  margin-left: 1rem;
}
@media (min-width: 768px) {
  footer .footer-grid .form-info .info-text {
    margin-left: 5rem;
    margin-left: 2rem;
  }
}
footer .footer-grid .form-info-social-networks {
  margin: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 320px) {
  footer .footer-grid .form-info-social-networks {
    flex-direction: row;
    justify-content: center;
  }
}
footer .footer-grid .form-info-social-networks .form-social-text {
  color: #ffffff;
  font-size: 2.5rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  footer .footer-grid .form-info-social-networks .form-social-text {
    margin-right: 3rem;
    font-size: 2.5rem;
    margin-bottom: unset;
  }
}
@media (min-width: 1201px) {
  footer .footer-grid .form-info-social-networks .form-social-text {
    font-size: 2.5rem;
  }
}
footer .footer-grid .form-info-social-networks .form-social-icons {
  display: flex;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  footer .footer-grid .form-info-social-networks .form-social-icons {
    margin-bottom: 0;
  }
}
footer .footer-grid .form-info-social-networks .form-social-icons .img-container {
  height: 3rem;
  margin: auto 1rem;
}
footer .footer-grid .form-info-social-networks .form-social-icons .img-container img {
  height: 100%;
}
footer .footer-grid .footer-3 {
  display: flex;
  justify-content: center;
}
@media (min-width: 481px) {
  footer .footer-grid .footer-3 {
    align-items: flex-end;
    justify-content: flex-end;
    grid-column: 1/3;
  }
}
@media (min-width: 1025px) {
  footer .footer-grid .footer-3 {
    grid-column: auto;
  }
}

.hero-img .img-container img {
  width: 100%;
  height: 20rem;
  object-fit: cover;
}
@media (min-width: 320px) {
  .hero-img .img-container img {
    height: 30rem;
  }
}
@media (min-width: 481px) {
  .hero-img .img-container img {
    height: 40rem;
  }
}
@media (min-width: 768px) {
  .hero-img .img-container img {
    height: 50rem;
  }
}
@media (min-width: 1025px) {
  .hero-img .img-container img {
    height: 60rem;
  }
}
@media (min-width: 1201px) {
  .hero-img .img-container img {
    height: 90rem;
  }
}

.home .hero-img {
  position: relative;
}
.home .hero-img .hero-text {
  width: 100%;
  position: absolute;
  bottom: 1.5rem;
}
@media (min-width: 320px) {
  .home .hero-img .hero-text {
    bottom: 4rem;
  }
}
@media (min-width: 768px) {
  .home .hero-img .hero-text {
    bottom: 6rem;
  }
}
@media (min-width: 1025px) {
  .home .hero-img .hero-text {
    bottom: 5rem;
  }
}
@media (min-width: 1201px) {
  .home .hero-img .hero-text {
    bottom: 8rem;
  }
}
.home .hero-img .hero-text h1 {
  width: 100%;
  text-align: center;
  color: #ffffff;
  font-family: "RomanSerif";
}
@media (min-width: 768px) {
  .home .hero-img .hero-text h1 {
    font-size: 3rem;
  }
}
@media (min-width: 1025px) {
  .home .hero-img .hero-text h1 {
    font-size: 4rem;
  }
}
.home .hero-img .hero-text .hero-btn {
  display: none;
}
@media (min-width: 481px) {
  .home .hero-img .hero-text .hero-btn {
    display: block;
  }
}
@media (min-width: 481px) {
  .home .hero-img .hero-text {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
  .home .hero-img .hero-text h1 span {
    display: block;
  }
  .home .hero-img .hero-text .hero-btn {
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: #ffffff;
    font-size: 1.25rem;
    margin: 2rem;
  }
  .home .hero-img .hero-text .hero-btn a {
    padding: 1rem;
    border: 2px solid #ffffff;
    border-radius: 2rem;
  }
  .home .hero-img .hero-text .hero-btn a:hover {
    color: #ffffff;
    background-color: #eb83a7;
  }
}
@media (min-width: 481px) and (min-width: 768px) {
  .home .hero-img .hero-text .hero-btn a {
    font-size: 2rem;
  }
}
@media (min-width: 481px) and (min-width: 1025px) {
  .home .hero-img .hero-text .hero-btn a {
    font-size: 3rem;
    padding: 1rem 3rem;
  }
}

.home-title h2 {
  margin: 4rem;
  text-align: center;
  color: #e76d98;
  font-family: "RomanSerif";
}
@media (min-width: 320px) {
  .home-title h2 {
    margin: 4rem 3rem;
  }
}
@media (min-width: 1025px) {
  .home-title h2 {
    margin: 10rem 3rem 5rem 3rem;
  }
}
.home-title p {
  text-align: center;
  margin: 3rem;
  color: #747474;
}
@media (min-width: 481px) {
  .home-title p {
    margin: 3rem 10rem 7rem;
  }
}
