@use "base/variables" as v;
@use "base/mixins" as m;

.hero-img {
    .img-container {
        img {
            width: 100%;
            height: 20rem;
            object-fit: cover;
            @include m.mobile {
                height: 30rem;
            }
            @include m.tablet {
                height: 40rem;
            }
            @include m.laptop {
                height: 50rem;
            }
            @include m.desktop {
                height: 60rem;
            }
            @include m.desktopXL {
                height: 90rem;
            }
        }
    }
}