@use "base/variables" as v;
@use "base/mixins" as m;

.contact {
  h2 {
    text-align: center;
    color: v.$primaryPinkDark;
    font-family: v.$text;
    // @include m.mobile {
    //     margin: 3rem;
    // }
  }
  .contact-squares {
    .squares-info {
      position: relative;
      background-color: v.$primaryPinkDark;
      .squares-text {
        padding: 3rem;
        text-align: center;
        color: v.$white;
      }
      @include m.mobile {
        margin: 3rem 15% 4rem 15%;
      }
      @include m.laptop {
        margin: 0;
        width: 40rem;
        padding: 0.5rem 2rem;
      }
      @include m.desktop {
        width: 55rem;
        margin-top: 7.5rem;
      }
    }
    .squares-photo {
      display: none;
      position: absolute;
      @include m.laptop {
        width: auto;
        height: 120%;
        right: -32rem;
        top: -10%;
        display: block;
        background-color: v.$white;
        z-index: -1;
        img {
          width: 100%;
          height: 100%;
        }
      }
      @include m.desktop {
        right: -37rem;
      }
      @include m.desktopXL {
        right: -42rem;
      }
    }
    @include m.laptop {
      width: 72rem;
      margin: auto;
      margin-bottom: 10rem;
      margin-top: 5rem;
    }
    @include m.desktop {
      width: 92rem;
    }
    @include m.desktopXL {
      width: 97rem;
    }
  }
}

.form-body {
  .contact-form {
    padding: 0.5rem;
    .contact-form-title {
      text-align: center;
      text-transform: uppercase;
      margin: 2rem 1rem 1rem 1rem;
      @include m.laptop {
        margin: 1rem auto 2rem auto;
        font-size: 2.5rem;
        padding-top: 4rem;
      }
      @include m.desktop {
        font-size: 3rem;
        margin: 1rem auto 2rem auto;
      }
      @include m.desktopXL {
        padding-top: 0;
        padding-top: 3rem;
        font-size: 3.5rem;
      }
    }
  }
}

.us-form {
  background-color: lighten($color: v.$primaryPink, $amount: 10);
  @include m.tablet {
    width: 70%;
    margin: 2rem auto;
  }
  @include m.desktop {
    width: 50%;
    margin: 2rem auto;
  }
  @include m.desktopXL {
    width: 40%;
    margin-bottom: 10rem;
  }
}

form {
  padding: 2.5rem 2rem;
  @include m.mobile {
    padding: 2.5rem 4rem;
  }
  @include m.tablet {
    padding: 2.5rem 3rem;
  }
  @include m.desktop {
    padding: 2.5rem 5rem;
  }
  .form-control::placeholder,
  select,
  .form-select,
  option {
    color: v.$primaryPinkDark;
  }
  .form-control,
  select {
    border: 1px solid v.$primaryPinkDark !important;
    border-radius: 1rem !important;
  }
  .aviso {
    padding-top: 0.5rem;
    color: v.$primaryPinkDark;
  }
  .btn-send {
    background-color: v.$primaryPinkDark;
    color: v.$white;
    text-transform: uppercase;
    //   font-family: v.$text;
    padding: 1rem 6rem;
    border-radius: 2rem;
    transition: all 0.5s;
    &:hover {
      // background-color: darken($color: v.$primaryBlueDark, $amount: 5);
      // color: v.$white;
      transition: all 0.5s;
    }
  }
  .form-control,
  .form-select {
    background-color: transparent;
    padding: 0.75rem;
    @include m.laptop {
      padding: 1.5rem;
      margin: 0.5rem;
    }
    @include m.desktop {
      padding: 0.75rem;
    }
    @include m.desktopXL {
      padding: 1.5rem;
    }
    //   font-family: v.$textNormal;
    //   color: v.$primaryPink;
    &:focus {
      border-color: v.$primaryPinkDark;
    }
  }
  //   label {
  //       color: v.$primaryPink;
  //   }
  a {
    //   color: v.$primaryBlue;
    &:hover {
      // color: v.$primaryBlueDark;
    }
  }
}

.form-check-input {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 1rem;
}

.form-control.is-invalid,
.was-validated .form-control:invalid {
  border-color: transparent;
  background-image: none;
  padding-right: 1rem;
}

.form-select.is-invalid:not([multiple]):not([size]),
.form-select.is-invalid:not([multiple])[size="1"],
.was-validated .form-select:invalid:not([multiple]):not([size]),
.was-validated .form-select:invalid:not([multiple])[size="1"],
.form-select.is-valid:not([multiple]):not([size]),
.form-select.is-valid:not([multiple])[size="1"],
.was-validated .form-select:valid:not([multiple]):not([size]),
.was-validated .form-select:valid:not([multiple])[size="1"] {
  padding-right: 1rem;
  border-color: transparent;
  //   background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e");
}

.invalid-feedback {
  padding-left: 0.5rem;
  // font-family: v.$text;
}

.form-control.is-valid,
.was-validated .form-control:valid {
  border-color: transparent;
  padding-right: 1rem;
  background-image: none;
}

.form-check {
  margin-left: 0.5rem;
}

.form-check-input {
  margin-top: 0.5rem;
}

.form-check-input:checked {
  background-color: v.$primaryPinkDark;
  border-color: v.$primaryPinkDark;
}

.form-control.is-valid,
.was-validated .form-control:valid {
  border-color: v.$primaryPinkDark;
}

.form-check-input.is-valid:checked,
.was-validated .form-check-input:valid:checked,
.form-check-input.is-valid,
.was-validated .form-check-input:valid {
  background-color: v.$primaryPinkDark;
  border-color: v.$primaryPink;
}
