// Tipografía

@font-face {
    font-family: 'RomanSerif';
    src: url('../fonts/RomanSerif.ttf') format('truetype'); 
}

@font-face {
    font-family: 'Briany';
    src: url('../fonts/Briany.otf') format('opentype'); 
}

@font-face {
    font-family: 'DidactGothic';
    src: url('../fonts/DidactGothic.ttf') format('truetype'); 
}

// Fonts

$text: 'RomanSerif';
$title: 'Briany';
$subtitle: 'DidactGothic';

// Colores

$white: #ffffff;
$primaryPink: #efc3d0;
$primaryPinkDark: #e76d98;
$primaryBeigeDark: #f3ad8a;
$primaryBeige: #ffddcf;
$secondaryBlueDark: #1da6cc;
$secondaryBlue: #1da6cc;
$secondaryGray: #747474;

// Tamaños de Media Queries

$mobile: 320px;
$tablet: 481px;
$laptop: 768px;
$desktop: 1025px;
$desktopXL: 1201px;
$desktopXXL: 1408px;
