@use "base/variables" as v;
@use "base/mixins" as m;

.technique {
  .hero-img {
    position: relative;
    h2 {
      position: absolute;
      bottom: 1rem;
      padding-left: 1rem;
      font-size: 1.5rem;
      @include m.mobile {
        padding-left: 2.5rem;
        bottom: 4rem;
        span {
          display: block;
          padding-left: 4rem;
          font-size: 1.75rem;
        }
      }
      @include m.tablet {
        font-size: 2.5rem;
        padding-left: 3rem;
        bottom: 5rem;
        span {
          font-size: 3rem;
        }
      }
      @include m.laptop {
        font-size: 3rem;
        padding-left: 7rem;
        bottom: 7rem;
        span {
          font-size: 3.5rem;
        }
      }
      @include m.desktop {
        font-size: 4rem;
        padding-left: 7rem;
        bottom: 7rem;
        span {
          font-size: 4.5rem;
        }
      }
      @include m.desktopXL {
        font-size: 5.5rem;
        padding-left: 8rem;
        bottom: 8rem;
        span {
          font-size: 6rem;
        }
      }
    }
    .h2-dark {
      color: v.$primaryPinkDark;
    }
    h2:not(.h2-dark) {
      color: v.$white;
    }
    .hero-logo {
      display: none;
      position: absolute;
      bottom: 17rem;
      width: 20%;
      padding-left: 10%;
      img {
        width: 100%;
      }
      @include m.tablet {
        display: block;
      }
      @include m.laptop {
        width: 25%;
        padding-left: 13%;
        bottom: 22rem;
      }
      @include m.desktopXL {
        bottom: 30rem;
      }
    }
  }
  .technique-description {
    margin: 3rem auto;
    @include m.tablet {
      margin-bottom: 5rem;
    }
  }
  h3 {
    text-align: center;
    color: v.$primaryPinkDark;
    padding: 1rem 2rem 0 2rem;
    @include m.tablet {
      padding-top: 3rem;
    }
  }
  h4 {
    text-align: center;
    color: v.$primaryPinkDark;
    padding: 1rem 2rem 0 2rem;
    @include m.tablet {
      padding-top: 3rem;
    }
  }
  p {
    text-align: center;
    margin: 3rem;
    color: v.$secondaryGray;
    @include m.tablet {
      margin: 3rem 10rem 0;
    }
    @include m.laptop {
      margin: 3rem 15rem 0;
    }
    @include m.desktopXL {
      font-size: 1.75rem;
      margin: 3rem 25rem 0;
    }
  }
  .technique-description-2 {
    h3 {
      margin-bottom: 3rem;
      @include m.tablet {
        margin-bottom: 6rem;
      }
    }
    p {
      margin: 1rem 2rem 2rem 2rem;
    }
    margin: auto 2rem 5rem 2rem;
    @include m.tablet {
      margin-left: 5rem;
      margin-right: 5rem;
    }
    @include m.laptop {
      margin-left: 15rem;
      margin-right: 15rem;
    }
    @include m.desktopXL {
      margin-left: 20rem;
      margin-right: 20rem;
    }
    .technique-square-1,
    .technique-square-2 {
      padding-bottom: 4rem;
      position: relative;
    }
    .technique-btn-1,
    .technique-btn-2 {
      position: absolute;
      width: 80%;
      margin: auto;
      height: 6rem;
      left: 10%;
      bottom: -3rem;
      display: flex;
      justify-content: center;
      align-items: center;
      a {
        border-radius: 3rem;
        padding: 0.5rem 2rem;
      }
    }
    .tecnhique-bg-1,
    .tecnhique-bg-2 {
      height: 3rem;
      width: 50%;
      z-index: -1;
      position: absolute;
      top: -1rem;
      right: 1rem;
      @include m.tablet {
        height: 10rem;
        top: -3rem;
        right: 1.5rem;
      }
    }
    .technique-square-1 {
      background-color: v.$primaryPinkDark;
      h4 {
        color: v.$primaryPink;
      }
      p {
        color: v.$white;
      }
    }
    .technique-btn-1 {
      background-color: v.$primaryBeige;
      a {
        color: v.$white;
        border: 2px solid v.$primaryPinkDark;
        background-color: v.$primaryPinkDark;
      }
    }
    .tecnhique-bg-1 {
      background-color: v.$primaryBeige;
    }
    .technique-square-2 {
      background-color: v.$primaryBeige;
      h4 {
        color: v.$primaryPinkDark;
      }
      p {
        color: v.$secondaryGray;
      }
    }
    .technique-btn-2 {
      background-color: v.$primaryPinkDark;
      a {
        color: v.$white;
        border: 2px solid v.$primaryBeige;
        background-color: v.$primaryBeige;
        color: v.$primaryPinkDark;
      }
    }
    .tecnhique-bg-2 {
      background-color: v.$primaryPinkDark;
    }
  }
}
