@use "base/variables" as v;
@use "base/mixins" as m;

.us {
  .hero-img {
    .img-container {
      position: relative;
      h2 {
        position: absolute;
        bottom: 30%;
        left: 40%;
        color: v.$primaryPinkDark;
        font-family: v.$text;
        text-align: right;
        span {
          display: block;
        }
        @include m.mobile {
          left: 40%;
          bottom: 35%;
          font-size: 3rem;
        }
        @include m.tablet {
          left: 45%;
          bottom: 35%;
          font-size: 4rem;
        }
        @include m.laptop {
          left: 50%;
          bottom: 35%;
          font-size: 5rem;
        }
        @include m.desktop {
          left: 45%;
          bottom: 35%;
          font-size: 10rem;
        }
      }
      img {
        height: auto;
      }
    }
  }
  .us-description,
  .us-contact {
    p {
      text-align: center;
      margin: 3rem;
      color: v.$secondaryGray;
      @include m.tablet {
        margin: 3rem 10rem 0rem;
      }
      @include m.laptop {
        margin: 3rem 15rem 0rem;
      }
      @include m.desktop {
        margin: 3rem 20rem 0rem;
      }
      @include m.desktopXL {
        max-width: 120rem;
        margin-left: auto;
        margin-right: auto;
      }
    }
    @include m.tablet {
      margin-bottom: 5rem;
      margin-top: 7rem;
    }
    @include m.laptop {
      margin-bottom: 5rem;
      margin-top: 10rem;
    }
    @include m.desktopXL {
      margin-top: 8rem;
      margin-bottom: 8rem;
    }
    @include m.desktopXL {
      margin-top: 10rem;
      margin-bottom: 10rem;
    }
  }
  .us-contact {
    margin-top: 20rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
    h3 {
      color: v.$primaryPinkDark;
      text-align: center;
      font-size: 1.8rem;
      padding: inherit 2rem;
      @include m.tablet {
        font-size: 2.5rem;
      }
      @include m.laptop {
        font-size: 3.5rem;
      }
      @include m.desktop {
        font-size: 4rem;
      }
    }
    a {
      // margin-top: 5rem;
      margin-top: 2rem;
      margin-bottom: 5rem;
      padding: 1rem;
      color: v.$primaryPinkDark;
      // border: 2px solid v.$white;
      border-radius: 4rem;
      font-size: 1.25rem;
      background-color: v.$white;
      &:hover {
        color: v.$white;
        background-color: lighten($color: v.$primaryPinkDark, $amount: 5);
      }
      @include m.mobile {
        margin-bottom: 7rem;
      }
      @include m.tablet {
        margin-top: 5rem;
        margin-bottom: 5rem;
      }
      @include m.laptop {
        font-size: 1.5rem;
      }
      @include m.desktop {
        font-size: 2rem;
        padding: 1rem 3rem;
        margin-bottom: 3rem;
      }
    }
    .bottom-img {
      width: 100%;
      height: 50rem;
      position: absolute;
      bottom: -5rem;
      z-index: -1;
      img {
        height: 100%;
        width: 100%;
      }
      @include m.desktop {
        height: 55rem;
        bottom: -10rem;
      }
    }
  }
}
